:root {
  --color-black: #111;
  --color-white: #fff;
  --color-blue: #4f2ee8;
  --color-yellow: #eac645;
  --color-grey-darker: #202024;
  --color-scroll: #e0e0e0;
  --color-grey-light: #b4afaf;
  --color-grey-lighter: #f6f6f6;
  --color-background: var(--color-grey-lighter);
  --color-header-background: var(--color-white);
  --color-header-border: var(--color-black);
  --color-text-primary: var(--color-black);
  --color-text-secondary: var(--color-grey-light);
  --color-category: rgba(17, 17, 17, .6);
  --color-category-active: var(--color-blue);
  --color-nav-scroll: var(--color-scroll);
  --color-backdrop: rgba(17, 17, 17, .4);
  --color-shop-card-background: var(--color-white);
  --color-shop-list-text: var(--color-black);
  --color-header-border: var(--color-black);
  --menu-icon-color: var(--color-black);
  --color-auth-form-background: var(--color-grey-lighter);
  --color-auth-form-border: var(--color-black);
  --close-icon-color: var(--color-black);
  --color-modal-background: var(--color-white);
  --color-modal-border: var(--color-black);
  --color-modal-cross: var(--color-black);
  --color-modal-title: var(--color-black);
  --color-modal-author: var(--color-grey-light);
  --color-modal-description: var(--color-black);
  --color-modal-shop: var(--color-black);
  --color-modal-btn: var(--color-black);
  --color-modal-info: rgba(17, 17, 17, .6);
  --font-family-primary: "DM Sans", sans-serif;
  --transition-timing-func: cubic-bezier(.4, 0, .2, 1);
  --transition-delay: 250ms;
  --transition-params: var(--transition-timing-func) var(--transition-delay);
}

.theme-dark {
  --color-text-primary: var(--color-white);
  --color-text-secondary: var(--color-grey-lighter);
  --color-category: rgba(255, 255, 255, .6);
  --color-category-active: var(--color-yellow);
  --color-nav-scroll: rgba(17, 17, 17, .6);
  --color-header-background: var(--color-black);
  --color-header-border: var(--color-white);
  --menu-icon-color: var(--color-white);
  --color-shop-card-background: var(--color-black);
  --color-shop-list-text: var(--color-white);
  --color-background: var(--color-grey-darker);
  --color-auth-form-background: #202024;
  --color-auth-form-border: var(--color-grey-lighter);
  --close-icon-color: var(--color-white);
  --color-modal-border: var(--color-white);
  --color-modal-background: var(--color-grey-darker);
  --color-modal-cross: var(--color-white);
  --color-modal-title: var(--color-white);
  --color-modal-author: var(--color-grey-light);
  --color-modal-description: var(--color-white);
  --color-modal-shop: var(--color-white);
  --color-modal-btn: var(--color-white);
  --color-modal-info: rgba(255, 255, 255, .6);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--font-family-primary);
  color: var(--color-text-primary);
  background-color: var(--color-background);
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 24px;
  }
}

.page-header {
  width: 100%;
  background-color: var(--color-header-background);
  z-index: 999;
  border: 1.5px solid var(--color-header-border);
  border-radius: 0 0 8px 8px;
  padding-top: 0;
  padding-bottom: 0;
  position: relative;
  top: 0;
  left: 0;
}

.page-header__container {
  min-height: 64px;
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .page-header__container {
    height: 72px;
  }
}

.logo {
  display: flex;
}

@media screen and (min-width: 768px) {
  .logo {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .logo {
    margin-right: 99px;
  }
}

.logo__link {
  align-items: center;
  gap: 8px;
  text-decoration: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  .logo__icon {
    width: 25px;
    height: 25px;
  }
}

.logo__text {
  fill: var(--menu-icon-color);
}

.menu {
  display: none;
}

@media screen and (min-width: 768px) {
  .menu {
    display: flex;
  }
}

.menu__list {
  align-items: center;
  gap: 24px;
  display: flex;
}

.menu__link {
  color: var(--menu-icon-color);
  font-family: var(--font-family-primary);
  text-transform: uppercase;
  letter-spacing: -.04em;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 36px;
  padding: 8px 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28;
  text-decoration: none;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.menu__link:hover, .menu__link:focus {
  background-color: var(--color-yellow);
  color: var(--color-black);
}

.menu__svg {
  fill: var(--menu-icon-color);
}

.shop-link:hover .menu__svg, .shop-link:hover .mobile-menu__svg {
  fill: var(--color-black);
}

.current {
  background-color: var(--color-yellow);
  color: var(--color-black);
}

.current .menu__svg {
  fill: var(--color-black);
}

.shop-link {
  align-items: center;
  gap: 4px;
  padding: 7px 16px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .shop-link {
    margin-top: 16px;
  }
}

.header-btn {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-btn {
    background-color: var(--color-blue);
    color: var(--color-white);
    letter-spacing: -.02em;
    border: none;
    border-radius: 36px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 4px 12px;
    font-size: 22px;
    line-height: 1.3;
    display: flex;
  }
}

.header-btn__circle {
  width: 37px;
  height: 37px;
  background-color: #f6f6f6;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-right: 9px;
  display: flex;
}

.header-btn__img {
  fill: var(--color-blue);
}

.header-btn__svg {
  fill: var(--color-white);
  margin-left: 4px;
  transform: rotate(180deg);
}

.switch {
  margin-left: auto;
  margin-right: 34px;
}

@media screen and (min-width: 768px) {
  .switch {
    margin-right: 24px;
  }
}

.switch__label {
  width: 40px;
  height: 20px;
  background: linear-gradient(180deg, var(--color-blue) 0%, #dcdcdc 100%);
  cursor: pointer;
  border-radius: 20px;
  display: block;
  position: relative;
  top: -8px;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switch__label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(180deg, var(--color-white) 0%, #f7f7f7 100%);
  border-radius: 18px;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switch__input {
  width: 0;
  height: 0;
  visibility: hidden;
}

.switch__input:checked + .switch__label:after {
  left: 39px;
  transform: translateX(-100%);
}

.menu-toggle {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
