  letter-spacing: -.04em;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 36px;
  padding: 8px 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28;
  text-decoration: none;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
